Template.WebMenu.onCreated(function(){
	var self = this;
	self.autorun(function (){
		self.subscribe('recipes');
	});
});

// Category One
Template.WebMenu.helpers({
	cat1col1: ()=> {
		return Recipes.find({"column":1, "category":"Category One","inMenu": true}, {sort: {"position": 1}});
	}
});
Template.WebMenu.helpers({
	cat1col2: ()=> {
		return Recipes.find({"column":2, "category":"Category One","inMenu": true}, {sort: {"position": 1}});
	}
});
Template.WebMenu.helpers({
	cat1col3: ()=> {
		return Recipes.find({"column":3, "category":"Category One","inMenu": true}, {sort: {"position": 1}});
	}
});
